
import { defineComponent } from 'vue';
import LogIn from '../components/LogIn.vue';


export default defineComponent({
  name: 'LandingPage',
  data() {
    return {
      user: "" as string
    }
  },
  methods: {
    applyUsername(name: string) {
      this.user = name;
    }
  },
  components: {
    LogIn
  }
});
